import { useQuery } from '@tanstack/react-query'
import React from 'react'
import { getLevelStudentStatistic, getLevelupStudentStatistic } from '../../../../api/Association/statisticAPI'
import LevelChart from '../../../../components/common/NivoCharts/LevelChart'
import { useDebug } from '../../../../hooks/basic/useDebug'

const LevelStudentStatistic = ({ year, month, region }) => {

  // query
  const { data: levelData } = useQuery(['getLevelStudentStatistic', year, month, region], () => getLevelStudentStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      let temp = []
      for (let i=0; i<9; i++) {
        temp.push({ level:`${i+1}급`, value: res[`geub_${i+1}_count`] })
      }
      return temp
    }
  })
  
  const { data: levelupData } = useQuery(['getLevelupStudentStatistic', year, month, region], () => getLevelupStudentStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      let temp = []
      for (let i=0; i<9; i++) {
        temp.push({ level:`${i+1}급`, value: res[`geub_${i+1}_levelup_count`] })
      }
      return temp
    }
  })

  // useDebug(levelupData)

  return (
    <>
      <LevelChart data1={levelData} data2={levelupData} type="급" />
    </>
  )
}

export default LevelStudentStatistic